body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#content_bin {
    margin-left: 175px;
    padding-top: 15px;
    width: calc( 100vw - 205px );
}
#root {
    height: 100vh;
    overflow-y: scroll;
}

#mobs-list .item {
    border: 1px solid #000;
    border-radius: 10px;
    margin: 1em 0;
    padding: 0.5em 0 0.5em 2em;
    text-align: left;
}
#mobs-list .item:hover,
#mobs-list .item.active {
    background-color: #000;
    color: #fff;
}
#mobs-list .item:hover .header,
#mobs-list .item.active .header {
    color: #fff;
}
    #mobs-list .item:hover .description,
    #mobs-list .item.active .description {
        color: rgba(255, 255, 255, 0.7);
    }

.field label {
    text-align: left;
}
header {
    padding: 0 5px;
    width: 70px;
}
.App-header {
    align-items: center;
    background-color: #282c34;
    color: white;
    display: flex;
    flex-direction: column;
    font-size: calc(10px + 2vmin);
    justify-content: center;
    min-height: 100vh;
    width: 150px;
}

    .App-header img.ui.image {
        height: auto;
        position: absolute;
        top: 10px;
        width: 150px;
    }
    .App-header a {
        color: #fff;
        display: block;
        font-family: 'Permanent Marker', cursive;
        margin: 0 0 3em 0;
        text-decoration: none;
    }

    .App-header nav {
        max-width: 100%;
    }

        .App-header nav .ui.vertical.text.menu .item {
            color: #fff;
            font-size: 20px;
            margin-bottom: 1em;
            margin-left: 25px;
        }

.Sidebar {
    position: absolute;
}
